  flex-wrap: wrap;
  overflow: auto;
  padding: 10px; }
  .stage.table-stage.row {
    flex-direction: row; }
    .stage.table-stage.row.left {
      justify-content: flex-start; }
    .stage.table-stage.row.center {
      justify-content: center; }
    .stage.table-stage.row.right {
      justify-content: flex-end; }
  .stage.table-stage.column {
    flex-direction: column; }
    .stage.table-stage.column.left {
      align-content: flex-start; }
    .stage.table-stage.column.center {
      align-content: center; }
    .stage.table-stage.column.right {
      align-content: flex-end; }
  .stage.table-stage .group {
